import Swiper, { Navigation, Pagination } from "swiper";
Swiper.use([, Navigation, Pagination]);
export default {
  async asyncData({ $axios, i18n }) {
    let page = 1;
    let pagination = 8;
    let params = {
      page,
      pagination,
    };
    params.lang = i18n.locale == "en" ? "en" : i18n.locale;
    const { data, meta } = (await $axios.get(`news`, { params })).data;
    const { last_page } = meta;
    return { data, page, pagination, last_page };
  },
  data: () => ({
    defaultImage: require("@/assets/images/qwe.png"),
    attrs: {
      class: "mb-6",
      boilerplate: true,
      elevation: 2,
    },
  }),
  methods: {
    goToInner(id) {
      this.$router.push(
        this.localePath({
          path: `/News/${id}`,
          query: {
            id: id,
          },
        })
      );
    },
    nextPage() {
      this.page++;
      this.getterNews();
    },
    prevPage() {
      this.page--;
      this.getterNews();
    },

    async getterNews() {
      let params = {
        page: this.page,
        pagination: this.pagination,
      };
      await this.$axios
        .get(`news`, { params })
        .then((res) => {
          this.data = res.data.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
};
